import React, { Component } from 'react'
import ReactTooltip from "react-tooltip";
import CustomToolTip from '../../../../containers/tooltip';
import { addAccountFromId } from '../../../../../../utils/addAccount';
import { GET_LOCAL } from '../../../../../../utils';
import { connect } from 'react-redux';
import { GET_FEEDS } from '../../../../../../actions/manageFeedsAction';

class ReconnectError extends Component {
    constructor() {
        super();
        this.controller = new AbortController();
    }

    handleMessage = async (event) => {
        console.log(event)
        const networkId = GET_LOCAL('networkId')
        console.log('networkId', networkId)
        const { error, code } = event.data;
        if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
            if (code) {
                await addAccountFromId(networkId, code, this.props.GET_FEEDS)
            }
        }
    };
    componentDidMount() {
        const signal = this.controller.signal;
        window.addEventListener("message", this.handleMessage, { signal });
    }
    componentWillUnmount() {
        // window.removeEventListener("message", this.handleMessage);
        this.controller.abort()
    }
    render() {
        const { btnTxt } = this.props
        return (
            <div className="d-flex align-items-center">
                <div className="d-flex align-items-center reconnect200791">
                    <CustomToolTip placement="top" overlayText="Click to resolve issue">
                        <div className="resetAction retry btn btn-danger btn-sm" title="">
                            <span className="reconnect_feed">
                                <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                    <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                        <rect x="0" y="0" width="24" height="24"></rect>
                                        <circle fill="#fff" cx="12" cy="12" r="10"></circle>
                                        <rect fill="#FF4051" x="11" y="10" width="2" height="7" rx="1"></rect>
                                        <rect fill="#FF4051" x="11" y="7" width="2" height="2" rx="1"></rect>
                                    </g>
                                </svg>
                                {btnTxt}
                            </span>
                        </div>
                    </CustomToolTip>
                </div>
            </div>
        )
    }
}


const mapStateToProps = state => {
    return {
    }
}

const mapDispatchToProps = dispatch => {
    return {
        GET_FEEDS: () => dispatch(GET_FEEDS()),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ReconnectError);