import reactImageSize from 'react-image-size';
import Swal from 'sweetalert2';
export const generateSocialName = (feed) => {
    var socialName = '';
    var name = feed.name;
    var prefix = "";
    if (String(name).includes("@")) {
        name = String(name).replace("@", " ");
    }
    if ([1, 2, 19].includes(feed.networkId)) {
        if (String(name).includes("#")) {
            name = String(name).replace("#", " ");
        }
    }


    switch (feed.networkId) {
        case 7:
            if (feed.filterId == 4 || feed.filterId == 32) {
                prefix = ""
                socialName += ' - Keywords';
            }
            break;
        case 19:
            prefix = ""
            if (feed.value3 != "") {
                name = `${name} (${feed.value3})`
            }
            break;
        case 1:
            if (feed.filterId == 7) {
                prefix = "@"
            }
            else if (feed.filterId == 4) {
                socialName += ' - Likes';
            }
            break;
        case 29:
            name = "SnapUp";
            prefix = "";
            break;
        case 18:
            if (feed.filterId == 33) {
                prefix = "";
            }
            break;
        case 10:
            if (feed.value3 == 0 || feed.value3 == 1) {
                socialName += ' - Company';
            }
            else if (feed.value3 == 2) {
                socialName += ' - Job';
            }
            break;

        default:
            break;
    }

    prefix = feedsNetworksPrefix(feed.filterId);
    socialName += `${prefix} ${(name && String(name).length > 0) ? name : feed.value1}`;

    switch (feed.filterId) {
        case 23:
            socialName += ' - Handle';
            break;
        case 24:
        case 62:
            socialName += ' - Mention';
            break;
        case 7:
            if (feed.networkId == 1) {
                socialName += ' - Mention';
            }
            break;

        case 25:
            socialName += ' - Tagged';
            break;

        case 34:
            socialName += ' - Story';
            break;

        case 8:
            socialName += ' - Page';
            break;

        case 66:
            socialName += ' - Videos';
            break;

        case 65:
            socialName += ' - Albums';
            break;

        case 63:
            socialName += ' - Sticker';
            break;

        case 74:
            socialName += ' - Reels';
            break;

        case 29:
            socialName += ' - My Places';
            break;

        case 55:
            socialName += ' - Page Reviews';
            break;

    }

    return socialName;

}


