export const check_hotspotLocation =(item)=>{
    let cnt = [];
    let top = 0;
     item.postproduct.map((prd)=>{
          if(prd.hostpot=="left:45%;top:45%;" ){
            cnt.push('a');
          }
          else if(prd.hostpot=="left:45%;top:50%;" ){
            cnt.push('b');
          }
          else if(prd.hostpot=="left:45%;top:40%;" ){
            cnt.push('c');
          }
          else if(prd.hostpot=="left:45%;top:55%;" ){
            cnt.push('d');
          }
          else if(prd.hostpot=="left:45%;top:35%;" ){
            cnt.push('e');
          }
          else if(prd.hostpot=="left:45%;top:60%;" ){
            cnt.push('f');
          }
          else if(prd.hostpot=="left:45%;top:30%;" ){
            cnt.push('g');
          }
          else if(prd.hostpot=="left:45%;top:65%;" ){
            cnt.push('h');
          }
          else if(prd.hostpot=="left:45%;top:25%;" ){
            cnt.push('i');
          }
          else if(prd.hostpot=="left:45%;top:70%;" ){
            cnt.push('j');
          }

          else{
            cnt.push('k');
          }
    })
   
    top = cnt.includes('a') ?  
               cnt.includes('b') ?  
                    cnt.includes('c') ? 
                       cnt.includes('d') ? cnt.includes('e') ? cnt.includes('f') ?  cnt.includes('g') ? cnt.includes('h') ? cnt.includes('i') ? '70%': '25%': '65%': '30%': '60%': '35%': '55%' : '40%' : '50%' : '45%'
  return top;
}

