export const SHOW_FEED_LIMIT_ERROR = () => {
    Swal.fire({
        // title: "Twitter",
        // text:'Connect to Twitter to pull the content for this feed.',
        imageUrl: 'https://cloud.taggbox.com/taggshop/app/assets/media/error/add-social-feed.png',
        title: '<strong>Add More Social Feeds</strong>',
        //   icon: 'info',
        width: 800,
        padding: '3em',
        html:
            'Oops! Looks like you have reached your feed limit. Now add more feeds without upgrading your entire plan and curate exciting widgets with diverse content.                ',
        showCloseButton: true,
        showCancelButton: false,
        focusConfirm: true,
        confirmButtonText:
            'Upgrade Now',
        confirmButtonAriaLabel: 'Upgrade Now',
        cancelButtonText:
            '<i class="fa fa-thumbs-down"></i>',
        cancelButtonAriaLabel: 'Thumbs down'
    }).then((result) => {
        if (result.value) {
            // this.getTwitterCode()
            window.showpricing(2,'Taggshop','taggshop');
        }
        else if (result.dismiss === "cancel") { }
    });
}