import React, { Component } from 'react'
import { CLOUDPATH, HOST_URL } from '../../../../../../actions/api'
import AccountSelect from './accountSelect'
import ReconnectError from './reconnect'
import Switch from './switch'
import HTMLReactParser from 'html-react-parser';
import { showAddAccounntAlert } from '../../../../../../utils'
import ReactTooltip from 'react-tooltip'


export default class FeedsRecord extends Component {
    constructor(props) {
        super(props)

        this.state = {
            moderation: this.props.feed.moderation,
            status: this.props.feed.status,
            //  showAccountList:false
        }
    }
    componentDidMount() {
        const { feed, changeStatus } = this.props
        this.setState({ status: feed.status, moderation: feed.moderation })
        //   this.checkaccountList(feed);
    }

    onModerationSwitch = (status) => {
        const { feed, changeStatus } = this.props
        changeStatus(feed.id, Number(status), 'moderation')
        this.setState({ moderation: Number(status) })
    }

    onFeedStatusSwitch = (status) => {
        const { feed, changeStatus } = this.props
        changeStatus(feed.id, Number(status), 'status')
        this.setState({ status: Number(status) })
    }
    // checkaccountList=(feed)=>{
    //    // var allowed_Flter_ids =  feed && feed.accountList && feed.filterId && feed.accountList.length && [1, 2, 3, 4, 5, 8, 65, 67, 26].includes(feed.filterId);
    //     var checkActiveAccount = feed && feed.accountList && feed.accountList.length && feed.accountList.filter(x => x.id ==feed.connectedAccount)
    //     var 
    // }

    reconnectAccount = (obj) => {
        const item = {
            id: obj.networkId,
            name: (obj.networkName).toLowerCase()
        }
        localStorage.setItem("feedId", obj.id)
        showAddAccounntAlert(item, true, true)
    }
    connectAccount = (obj) => {
        const item = {
            id: obj.networkId,
            name: (obj.networkName).toLowerCase()
        }
        localStorage.setItem("feedId", obj.id)
        showAddAccounntAlert(item, false, true)
    }


    render() {
        const { feed, onFeedDelete, changeAccount } = this.props
        const { status, moderation } = this.state
        return (
            <>
                <td className="text-nowrap feed_status__ inactive_feed">
                    <div className="d-flex align-items-center flex-lg-nowrap flex-wrap justify-content-between">
                        <div className="d-flex align-items-center">
                            <div className="flex-shrink-0 mr-4 mt-lg-0 d-flex align-items-center">
                                <div className="symbol symbol-30">
                                    <img className="img-fluid mCS_img_loaded" src={`${CLOUDPATH}taggshop/app/assets/media/social-icons/social-icons-id/${feed.networkId}.svg?ver=1`} alt={feed.networkName} />
                                </div>
                            </div>
                            <div className="d-flex flex-column text-left overflow-hidden mr-2">
                                <p title={feed.tooltip} className="text-dark m-0 d-flex manageFeed_title">
                                    <span className="text-nowrap overflow-hidden text-ellipsis" style={{ maxWidth: "170px" }}>
                                        {feed.name == 'onsiteupload' ? 'SnapUp'  : HTMLReactParser(feed.name)}
                                    </span>
                                    {feed.socialLink ? <a href={feed.socialLink} target="_blank">
                                        <i className="fas fa-external-link-alt icon-nm ml-1"></i>
                                    </a> : null}
                                </p>
                                <p className="font-weight-normal text-muted mb-0 text-nowrap manageFeed_networkName">
                                    {feed.networkName}
                                </p>
                            </div>
                            {feed.isBtntext == "Reconnect" ?
                                <span onClick={() => this.reconnectAccount(feed)}>
                                    <ReconnectError btnTxt={"Reconnect"} />
                                </span>
                                : feed.isBtntext == "Connect" ? <span onClick={() => this.connectAccount(feed)}>
                                    <ReconnectError btnTxt={"Connect"} />
                                </span> : null}

                                {/* <div class="d-flex align-items-center"><div class="d-flex align-items-center reconnect" data-test-id="connect-Instagram Business-#tuinmeubelshop"><span><div class="d-flex align-items-center"><div class="d-flex align-items-center reconnect200791"><div class="d-flex mx-2 align-items-center"><span class="svg-icon svg-icon-secondary svg-icon-md">*/}
                                {feed.cronStatus ?
                                 <><span className="svg-icon svg-icon-sm svg-icon-dark tooltips ms-2" data-tip="React-tooltip" data-for="totalPost"> 
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                        <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                        <rect x="0" y="0" width="24" height="24"></rect>
                                        <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"></circle>
                                        <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"></rect>
                                        <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"></rect>
                                        </g>
                                    </svg>
                                    </span>
                                    <ReactTooltip id="totalPost" place="top" type="dark" effect="solid" className="text-wrap">
                                    {feed.cronStatus}
                                    </ReactTooltip>
                                    </>
                                    : <></>}
                                    {/* </span></div></div></div></span></div></div> */}
                        </div>
                        <div className="d-flex align-items-center">
                            <div className="d-flex align-items-center reconnect179667"></div>
                        </div>
                    </div>
                </td>
                <td className="feed_status__ inactive_feed user_row__">

                    {feed && feed.accountList && feed.accountList.length ?
                        feed.filterId && [1, 2, 3, 4, 5, 8, 65, 67, 26].includes(feed.filterId) && feed.networkId != 2 ?
                            feed.accountList.filter(x => x.id == feed.connectedAccount) ? <>

                                <AccountSelect changeAccount={changeAccount} feed={feed} key={feed.id} />
                            </>
                                : ''
                            : ''
                        : ''}
                </td>
                <td className="justify-content-center toggleOnBut feed_status__ inactive_feed">
                    <Switch value={moderation} setValue={e => this.onModerationSwitch(e)} id={`moderationSwitch_${feed.id}`} />
                </td>
                <td className="feed_status__ inactive_feed">
                    <div className="text-center">
                        <a className="text-dark" href={`${HOST_URL}content/moderation/${localStorage.getItem('wallId')}/feed/${feed.id}`} target="_blank">
                            <p className="text-dark m-0 text-nowrap">
                                <strong>{feed.postCount}</strong>
                            </p>
                            <p className="font-weight-normal text-muted mb-0 text-nowrap">
                                Approved {feed.postCount > 1 ? 'Posts' : 'Post'}
                            </p>
                        </a>
                    </div>
                </td>
                <td className="justify-content-center toggleOnBut">
                    <Switch value={status} setValue={e => this.onFeedStatusSwitch(e)} id={`FeedStatusSwitch${feed.id}`} />
                </td>
                <td className="text-center">
                    <button className="btn btn-link btn-icon btn-sm btn-clean maintainPopUp resetAction delete tbl-action-btn py-0" title="delete" onClick={() => onFeedDelete(feed.id)}>
                        <span className="svg-icon-lg svg-icon-dark-75">
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                <g data-name="Group 4331" transform="translate(10956 -4533)">
                                    <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                    <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                        <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#FF4051" fillRule="evenodd"></path>
                                    </g>
                                </g>
                            </svg>
                        </span>
                    </button>
                </td>
            </>
        )
    }
}

