import React, { Component } from 'react' 
import { connect } from 'react-redux'
import { ACTION_DELETE_FEED, ACTION_FEED_STATUS , ACTION_UPDATE_SOCIAL_ACCOUNT} from '../../../../../../actions/manageFeedsAction'
import FeedsRecord from './feedsRecord'
import ReconnectError from './reconnect'
import Swal from 'sweetalert2'
import { getModeration } from '../../../../../../actions/moderationActions'


class FeedTable extends Component {
    state={currentPage:1, showPost:10, totalPage:1, allFeeds:[], currentPageFeeds:null}


    componentDidMount(){  this.countAllPage();  }

    changeTotalPost=(e)=>{this.setState({showPost:parseInt(e.target.value), currentPage:1},()=>this.countAllPage())}
    nextPrev=(no)=>{ this.setState({currentPage:no} ,()=>this.countAllPage()) } 

    countAllPage=()=>{ 
        const { feeds } = this.props;  
        var i = 0 , newFeeddata = [], n=feeds.length
        while (i < n) {newFeeddata.push(feeds.slice(i, i += this.state.showPost)) }   
        setTimeout(this.setState({allFeeds:newFeeddata, totalPage:newFeeddata.length}),100)
    }


    changeStatus = (feedId, status , action) => {
        this.props.ACTION_FEED_STATUS(feedId, status, action, this.onSuccessUpdateRedux)
    }


    changeAccount = (accountId, feedId) => { 
        this.props.ACTION_UPDATE_SOCIAL_ACCOUNT(accountId, feedId, this.onSuccessUpdateRedux)
    }



    onFeedDelete = (feedId) => {
        Swal.fire({
            icon: 'warning',
            title: 'Are you sure?', 
            showCloseButton: true,
            showCancelButton: true,
            focusConfirm: true,
            confirmButtonText: 'Yes',
            confirmButtonAriaLabel: 'Yes',
            cancelButtonText: 'No',
            cancelButtonAriaLabel: 'Thumbs down'
        }).then((result) => {
            if (result.value) {
               this.props.ACTION_DELETE_FEED(feedId, this.onSuccessUpdateRedux)
            }
            else if (result.dismiss === "cancel") { }
        });
    }
    onSuccessUpdateRedux=()=>{  this.props.getModeration(); this.countAllPage() }
 

    render() { 
        const { currentPage, showPost, totalPage , allFeeds } = this.state
        const { feeds } = this.props;  
        

        return (
            <div className="table-scrollable globalTblList h-100">
                <div id="feed_datatable_wrapper" className="dataTables_wrapper no-footer d-flex justify-content-between flex-column h-100">
                    <div className="col-sm-12 px-0">
                        <table id="feed_datatable" className="table table-hover dataTable no-footer" aria-describedby="feed_datatable_info">
                            <thead>
                                <tr>
                                    <th className="text-left sorting_disabled" rowSpan="1" colSpan="1" style={{ width: "389.462px" }}>Name</th>
                                    <th className="text-center sorting_disabled" rowSpan="1" colSpan="1" style={{ width: "301.684px" }}>Account</th>
                                    <th className="text-center sorting_disabled" rowSpan="1" colSpan="1" style={{ width: "111.337px" }}>Moderation</th>
                                    <th className="text-center sorting_disabled" rowSpan="1" colSpan="1" style={{ width: "148.316px" }}>Posts</th>
                                    <th className="text-center sorting_disabled" rowSpan="1" colSpan="1" style={{ width: "66.684px" }}>Status</th>
                                    <th className="text-center sorting_disabled" rowSpan="1" colSpan="1" style={{ width: "66.6146px" }}>Action</th></tr>
                            </thead>
                            <tbody>
                                {allFeeds && allFeeds.length && allFeeds[currentPage-1] && allFeeds[currentPage-1].length ? allFeeds[currentPage-1].map((feed, index) => (
                                    <tr className="gradeX odd" key={feed.id} id={`feedsrecord${feed.id}`}>
                                        <FeedsRecord feed={feed}  changeStatus={this.changeStatus} onFeedDelete={this.onFeedDelete} changeAccount={this.changeAccount} key={`tr_${feed.id}`}/>
                                    </tr>
                                )) : null}
                                
                            </tbody>
                        </table>
                    </div>
                    <div className="col-sm-12">
                        <div className="row pt-4 border-top">
                            <div className="col-sm-12 col-md-5">
                                <div className="dataTables_info" id="feed_datatable_info">
                                    Showing {currentPage==1 ? currentPage : ((currentPage-1)* showPost)+1} to {showPost < feeds.length ? showPost*currentPage : feeds.length} of {feeds ? feeds.length : 0}	entries
                                </div>
                            </div>
                            <div className="col-sm-12 col-md-7 dataTables_pager">
                                <div className="dataTables_length" id="feed_datatable_length">
                                    <label>Show
                                        <select name="feed_datatable_length"   onChange={e => this.changeTotalPost(e)}>
                                            <option value="5" selected={showPost==5 ? true: false}>5</option>
                                            <option value="10" selected={showPost==10 ? true: false}>10</option>
                                            <option value="15" selected={showPost==15 ? true: false}>15</option>
                                            <option value="20" selected={showPost==20 ? true: false}>20</option>
                                            <option value="30" selected={showPost==30 ? true: false}>30</option>
                                            <option value="40" selected={showPost==40 ? true: false}>40</option>
                                        </select>
                                        entries
                                    </label>
                                </div>
                                <div className="dataTables_paginate paging_simple_numbers" id="feed_datatable_paginate">
                                    <button className={`paginate_button previous ${currentPage == 1 ? "disabled" :''} `} disabled={currentPage == 1 ? true :false}  id="feed_datatable_previous"  onClick={()=>{this.nextPrev(currentPage-1)}}>Previous</button>
                                    
                                    {allFeeds && allFeeds.length ? allFeeds.map((allFeeds ,i) => 
                                    <span key={i}>
                                         <button className={`paginate_button ${currentPage== i+1 ? "current" :''}`}  onClick={()=> this.setState({currentPage: i+1})}>{i+1}</button>
                                    </span> ):
                                    <span><button className="paginate_button current">1</button></span>}
                                   
                                    <button className={`paginate_button next ${currentPage == totalPage ? "disabled" :''} `}  disabled={currentPage == totalPage ? true :false}   id="feed_datatable_next" onClick={()=>{this.nextPrev(currentPage+1) }}>Next</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps=state =>{
    return {
        feeds: state.manageFeeds.feeds
    }
}

const mapDispatchToProps= dispatch =>{
    return{
        ACTION_DELETE_FEED: (feedId, callback) => dispatch(ACTION_DELETE_FEED(feedId, callback)),
        ACTION_FEED_STATUS: (feedId, status, action, callback) => dispatch(ACTION_FEED_STATUS(feedId, status, action, callback)),
        ACTION_UPDATE_SOCIAL_ACCOUNT: (accountId, feedId, callback) => dispatch( ACTION_UPDATE_SOCIAL_ACCOUNT(accountId, feedId, callback)),
        getModeration: () => dispatch(getModeration()),
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(FeedTable)